.form {
  width: 410px;
  min-height: 182px;
  margin-right: 169px;
  margin-top: 64px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  color: $white;
}

.form input:invalid {
  box-shadow: none;
}

.form__text {
  width: 410px;
  @include marginnone;
  min-height: 37px;
  margin-bottom: 34px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 49px;
  line-height: 52px;
  letter-spacing: 0.03em;
  text-transform: lowercase;
}

.form__wrap {
  width: 410px;
  min-height: 22px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  line-height: 16px;
}

.form__input {
  width: 190px;
  height: 22px;
  margin-bottom: 14px;
  padding: 6px 0 6px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  line-height: 18px;
  border: none;
  background: transparent;
  color: $white;
  border-bottom: 1px solid #74819C;
  outline: none;


  &::placeholder {
    font-size: 14px;
    color: $white;
  }

  &:first-child {
    margin-right: auto;
  }
}

.form__button {
  width: 408px;
  height: 54px;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid $white;
  background: transparent;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $blue-black;
    border: 3px solid $blue-black;
  }
}

@media (min-width: $tablet-width) and (max-width: $desktop-width) {
  .form {
    width: 474px;
    margin-top: 68px;
    max-height: 184px;
    margin-right: 4%;
  }

  .form__text {
    width: 474px;
    font-size: 42px;
    line-height: 42px;
    margin-bottom: 32px;
    letter-spacing: 0.037em;
  }

  .form__wrap {
    width: 472px;
  }

  .form__input {
    width: 206px;
    padding-bottom: 3px;
    margin-bottom: 21px;
  }

  .form__button {
    width: 472px;
    margin-top: 10px;
  }
}

@media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
  .form {
    width: 290px;
    min-height: 249px;
    margin: 0 auto;
  }

  .form__text {
    width: 290px;
    min-height: 24px;
    margin-bottom: 32px;
    font-size: 32px;
    line-height: 32px;
  }

  .form__wrap {
    width: 290px;
    min-height: 82px;
    flex-direction: column;
    align-items: center;
  }

  .form__input {
    width: 290px;
    padding-bottom: 5px;
    margin-bottom: 26px;
  }

  .form__button {
    width: 290px;
    padding: 0;
    margin-top: 12px;

  }
}
